@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  font-family: "Fjalla One";
}

body header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 4rem;
}

body header .logo {
  background-size: 100%;
  width: 200px;
  height: 60px;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
}

body header .search {
  display: flex;
}

body header .search .lista {
  display: flex;
}

body header .search .lista .selector {
  width: 150px;
}

body header .search .lista :active .navSearchDropdown {
  display: unset;
}

body header .FaOpencart {
  height: 40px;
  width: 40px;
  color: orange;
}

body .main {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 90vh;
}

body .main .textoDeCarga {
  margin: auto;
}

body .main .gridContainer {
  width: 80%;
  margin: 50px;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 33% 33% 33%;
      grid-template-columns: 33% 33% 33%;
}

body .main .gridContainer .gridItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

body .main .gridContainer .gridItem img {
  width: 50%;
}

body .main .gridContainer .gridItem input {
  text-align: center;
}

body .main .gridContainer .gridItem .selector {
  display: flex;
  justify-content: space-between;
}

body .main .gridContainer .gridItem p {
  width: 65%;
}

